function load_wysiwyg(id, options) {
    // todo: get rid of base64 encodings, upload with ftp.js
    let editor = new FroalaEditor('#' + id, options, function() {
        editor.events.on('image.loaded', function($img) {

            var image = $img[0];
            if (image.hasAttribute('converted')) return;

            var canvas = document.createElement('canvas'),
                ctx = canvas.getContext('2d');
            
            canvas.width = image.naturalWidth;
            canvas.height = image.naturalHeight;
            
            ctx.drawImage(image, 0, 0);

            dataURI = canvas.toDataURL(); 
            
            image.src = dataURI;
            image.setAttribute('converted', true);
        });

    });

}
