image_upload_ready = function() {
    if ($.fn.cloudinary_fileupload !== undefined && !window.loaded) {
        $("input.cloudinary-fileupload[type=file]").cloudinary_fileupload({
            disableImageResize: false,
            // imageMaxWidth: 800,                   // 800 is an example value - no default
            // imageMaxHeight: 600,                  // 600 is an example value - no default
            maxFileSize: 5000000, // 5MB is an example value - no default
            loadImageMaxFileSize: 20000000, // default is 10MB
            acceptFileTypes: /(\.|\/)(gif|jpe?g|png|bmp|ico)$/i
        });
    }
    
    $('.cloudinary-fileupload').bind('cloudinarydone', function(e, data) {
	try {
	    var Input = e.target.parentNode.querySelector('.file-url-input');
	    Input.value = data.result.url;
	    yo_image_upload_preview(Input);
	    mws_removeClass(e.target.parentNode.parentNode.parentNode.querySelector('button.submit'), 'is-loading');
	    mws_forEach(e.target.parentNode.querySelectorAll('button.is-loading'), function(el) {
		mws_removeClass(el, 'is-loading');
	    });
	} catch (err) {console.log("err", err);}
	return true;
    });
    
    $('.cloudinary-fileupload').bind('fileuploadsend', function(e, data) {
	try {
	    mws_addClass(e.target.parentNode.parentNode.parentNode.querySelector('button.submit'), 'is-loading');
	    mws_forEach(e.target.parentNode.querySelectorAll('button'), function(el) {
		mws_addClass(el, 'is-loading');
	    });
	} catch (err) {console.log("err", err);}
    });
    
    //$('.cloudinary-fileupload').bind('cloudinaryprogress', function(e, data) {});
    
}

$(document).ready(function() {
    image_upload_ready();
});

